 * @author Takuto Yanagida
 * @version 2021-09-25
 */


@use "sass:math";


:root {
	font-size  : 16px;
	font-family: -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
}

body {
	min-height    : 200vh;
	padding-top   : 4vh;
	padding-bottom: 4vh;
	overflow-x    : hidden;

	background-color: #fff;
}

h1 small {
	font-size: small;
}

pre {
	margin: 0;
	overflow-x: auto;
}

.content {
	margin   : { left: auto; right: auto; }
	max-width: 48rem;
	width    : calc(100% - 2vw);

	color: hsla(0, 0%, 0%, 0.75);
}


// -----------------------------------------------------------------------------


.frame-global,
.frame-priority {
	margin-top: 2rem;

	.current > * {
		background-color: #edf;
	}
	.hover > * {
		background-color: #eee;
	}
}
